::-webkit-scrollbar {
  width: 6px;
  background-image: linear-gradient(to bottom, rgba(134, 134, 134, 0.45), rgba(134, 134, 134, 0.09));
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-image: linear-gradient(0deg, #f79046 0.28%, rgba(247, 144, 70, 0) 100%), url("libraries/styles||file:../../assets/images/content/scroll-shadow.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border: 1px solid transparent;
}

